"use client";

import { useState } from "react";
import { Plus, Trash2, Pill } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

export interface PrescriptionItemData {
  medicine_name: string;
  dosage: string;
  frequency: string;
  duration: string;
  quantity: number;
  instructions: string;
}

interface PrescriptionFormProps {
  items: PrescriptionItemData[];
  onChange: (items: PrescriptionItemData[]) => void;
  disabled?: boolean;
}

const defaultItem: PrescriptionItemData = {
  medicine_name: "",
  dosage: "",
  frequency: "",
  duration: "",
  quantity: 1,
  instructions: "",
};

// Common frequencies
const FREQUENCIES = ["1x1", "2x1", "3x1", "4x1", "1x2", "2x2", "3x2", "Jika perlu"];

// Common durations
const DURATIONS = ["3 hari", "5 hari", "7 hari", "10 hari", "14 hari", "1 bulan"];

export function PrescriptionForm({ items, onChange, disabled }: PrescriptionFormProps) {
  const addItem = () => {
    onChange([...items, { ...defaultItem }]);
  };

  const removeItem = (index: number) => {
    const newItems = items.filter((_, i) => i !== index);
    onChange(newItems);
  };

  const updateItem = (index: number, field: keyof PrescriptionItemData, value: string | number) => {
    const newItems = [...items];
    newItems[index] = { ...newItems[index], [field]: value };
    onChange(newItems);
  };

  return (
    <Card>
      <CardHeader className="pb-4">
        <CardTitle className="text-lg flex items-center justify-between">
          <span className="flex items-center gap-2">
            <Pill className="h-5 w-5 text-green-500" />
            Resep Obat
          </span>
          <Button type="button" size="sm" variant="outline" onClick={addItem} disabled={disabled}>
            <Plus className="h-4 w-4 mr-1" />
            Tambah Obat
          </Button>
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        {items.length === 0 ? (
          <div className="text-center py-8 text-muted-foreground border-2 border-dashed rounded-lg">
            <Pill className="h-12 w-12 mx-auto mb-3 opacity-50" />
            <p>Belum ada obat ditambahkan</p>
            <Button type="button" variant="link" onClick={addItem} disabled={disabled}>
              + Tambah Obat
            </Button>
          </div>
        ) : (
          items.map((item, index) => (
            <div key={index} className="p-4 border rounded-lg space-y-3 relative">
              <Button
                type="button"
                size="icon"
                variant="ghost"
                className="absolute top-2 right-2 h-8 w-8 text-red-500 hover:text-red-700"
                onClick={() => removeItem(index)}
                disabled={disabled}
              >
                <Trash2 className="h-4 w-4" />
              </Button>

              <div className="pr-10">
                <Label>Nama Obat *</Label>
                <Input
                  placeholder="Contoh: Paracetamol 500mg"
                  value={item.medicine_name}
                  onChange={(e) => updateItem(index, "medicine_name", e.target.value)}
                  disabled={disabled}
                />
              </div>

              <div className="grid gap-3 sm:grid-cols-4">
                <div>
                  <Label>Dosis</Label>
                  <Input
                    placeholder="500mg"
                    value={item.dosage}
                    onChange={(e) => updateItem(index, "dosage", e.target.value)}
                    disabled={disabled}
                  />
                </div>
                <div>
                  <Label>Frekuensi</Label>
                  <Input
                    placeholder="3x1"
                    value={item.frequency}
                    onChange={(e) => updateItem(index, "frequency", e.target.value)}
                    disabled={disabled}
                    list={`freq-${index}`}
                  />
                  <datalist id={`freq-${index}`}>
                    {FREQUENCIES.map((f) => (
                      <option key={f} value={f} />
                    ))}
                  </datalist>
                </div>
                <div>
                  <Label>Durasi</Label>
                  <Input
                    placeholder="5 hari"
                    value={item.duration}
                    onChange={(e) => updateItem(index, "duration", e.target.value)}
                    disabled={disabled}
                    list={`dur-${index}`}
                  />
                  <datalist id={`dur-${index}`}>
                    {DURATIONS.map((d) => (
                      <option key={d} value={d} />
                    ))}
                  </datalist>
                </div>
                <div>
                  <Label>Jumlah</Label>
                  <Input
                    type="number"
                    min="1"
                    placeholder="10"
                    value={item.quantity}
                    onChange={(e) => updateItem(index, "quantity", parseInt(e.target.value) || 1)}
                    disabled={disabled}
                  />
                </div>
              </div>

              <div>
                <Label>Aturan Pakai / Instruksi</Label>
                <Input
                  placeholder="Contoh: Setelah makan"
                  value={item.instructions}
                  onChange={(e) => updateItem(index, "instructions", e.target.value)}
                  disabled={disabled}
                />
              </div>
            </div>
          ))
        )}
      </CardContent>
    </Card>
  );
}
